/* CategoryCard.css */

.category-card {
  width: 150px;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ddd;
  overflow: hidden;
  cursor: pointer;
}

.category-image {
  height: 100px;
  background-size: cover;
  background-position: center;
  border-radius: 8px;
  margin-bottom: 10px;
}

.category-name {
  text-align: center;
  font-size: 0.9rem;
  color: #555;
}
